@charset "UTF-8";
*, *::before, *::after {
  box-sizing: border-box;
}

:root {
  word-wrap: break-word;
  word-break: break-all;
  overflow-wrap: break-word;
  font-size: calc(1em * .625);
  font-family: "Noto Sans JP", "Hiragino Kaku Gothic Pro", Meiryo, "メイリオ", "MS PGothic", "MS Pゴシック", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  min-width: 320px;
  width: 100%;
  background: #fff8ea;
  color: #362519;
  font-size: 1.5rem;
  line-height: 1.5;
}
@media screen and (min-width: 769px) {
  body {
    font-size: 1.6rem;
  }
}
body.is-fixed {
  position: fixed;
  overflow-x: hidden;
  overflow-y: scroll;
  height: 100vh;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, button, input, textarea, select, option, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
}

pre, code, kbd, samp {
  font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
}

a {
  color: inherit;
  text-decoration: none;
  font-size: inherit;
}

small {
  color: inherit;
  font-size: inherit;
}

b, em, strong {
  font-weight: bold;
  font-style: normal;
}

i {
  font-style: italic;
}

img {
  max-width: 100%;
  height: auto;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote::before, blockquote::after {
  content: "";
  content: none;
}

q::before, q::after {
  content: "";
  content: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
}

label {
  cursor: pointer;
}

input[type=text], textarea {
  font-family: inherit;
  appearance: none;
}
input[type=text]:-ms-input-placeholder, textarea:-ms-input-placeholder {
  color: inherit;
}

input[type=checkbox], input[type=radio], ::-ms-check {
  appearance: none;
}

button, input[type=submit] {
  border: none;
  background: transparent;
  cursor: pointer;
}

hr {
  margin: 20px 0;
  border: none;
  border-bottom: solid 1px #ddd;
}

ruby {
  letter-spacing: 2px;
  font-size: 1rem;
}

rt {
  font-size: 1.2rem;
}

select {
  background: none;
  appearance: none;
}

select::-ms-expand {
  display: none;
}

::-moz-selection {
  background: rgba(0, 100, 180, .33);
}

::selection {
  background: rgba(0, 100, 180, .33);
}

main, article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

svg {
  pointer-events: none;

  fill: currentColor;
}

use {
  display: inline;
}

[hidden] {
  display: none;
}

/* :root[data-use-keyboard]
----------------------------------------------- */
:root[data-use-keyboard=false] :focus {
  outline: 0 !important;
}

/* noto-sans-jp-regular - japanese */
@font-face {
  font-weight: 400;
  font-style: normal;
  font-family: "Noto Sans JP";
  src: local("Noto Sans Japanese Regular"), local("NotoSansJapanese-Regular"), url("/assets/fonts/noto-sans-jp-v25-japanese-regular.woff2") format("woff2"), url("/assets/fonts/noto-sans-jp-v25-japanese-regular.woff") format("woff");
  font-display: swap;
  /* Modern Browsers */
}
/* noto-sans-jp-700 - japanese */
@font-face {
  font-weight: 700;
  font-style: normal;
  font-family: "Noto Sans JP";
  src: local("Noto Sans Japanese Bold"), local("NotoSansJapanese-Bold"), url("/assets/fonts/noto-sans-jp-v25-japanese-700.woff2") format("woff2"), url("/assets/fonts/noto-sans-jp-v25-japanese-700.woff") format("woff");
  font-display: swap;
  /* Modern Browsers */
}
.highlight {
  position: relative;
  overflow: auto;
  padding: 40px 16px 16px;
  background-color: #272822;
  color: #fff;
}
.highlight:not(:first-child) {
  margin-top: 20px;
}
.highlight > .codehilite:first-child {
  margin-top: -20px;
}
.highlight .highlight-name {
  position: absolute;
  top: 0;
  left: 0;
  padding: 6px 10px;
  background: #888;
  color: #fff;
  line-height: 1;
}
.highlight .lineno span {
  opacity: .25;
}
.highlight table td {
  padding: 5px;
}
.highlight table pre {
  margin: 0;
}
.highlight .gh {
  color: #999;
}
.highlight .sr {
  color: #f6aa11;
}
.highlight .go {
  color: #888;
}
.highlight .gp {
  color: #555;
}
.highlight .gu {
  color: #aaa;
}
.highlight .nb {
  color: #f6aa11;
}
.highlight .cm, .highlight .cp, .highlight .c1, .highlight .cs, .highlight .c, .highlight .ch, .highlight .cd, .highlight .cpf {
  color: #75715e;
}
.highlight .err, .highlight .gr, .highlight .gt {
  color: #960050;
}
.highlight .gd, .highlight .gi, .highlight .ge {
  color: #49483e;
}
.highlight .kc, .highlight .kd, .highlight .kr, .highlight .no, .highlight .kt {
  color: #66d9ef;
}
.highlight .mf, .highlight .mh, .highlight .il, .highlight .mi, .highlight .mo, .highlight .m, .highlight .mb, .highlight .mx, .highlight .sc, .highlight .se, .highlight .ss {
  color: #ae81ff;
}
.highlight .sd, .highlight .s2, .highlight .sb, .highlight .sh, .highlight .si, .highlight .sx, .highlight .s1, .highlight .s, .highlight .sa, .highlight .dl {
  color: #e6db74;
}
.highlight .na, .highlight .nc, .highlight .nd, .highlight .ne, .highlight .nf, .highlight .fm {
  color: #a6e22e;
}
.highlight .vc, .highlight .nn, .highlight .nl, .highlight .ni, .highlight .bp, .highlight .vg, .highlight .vi, .highlight .nv, .highlight .vm, .highlight .w, .highlight .n, .highlight .py, .highlight .nx {
  background-color: #272822;
  color: #fff;
}
.highlight .ow, .highlight .nt, .highlight .k, .highlight .kv, .highlight .kn, .highlight .kp, .highlight .o {
  color: #f92672;
}

/* ----------------------------------------------------------------
    Header
----------------------------------------------------------------- */
/*
----------------------------------------------- */
.l-header {
  position: relative;
  overflow: hidden;
  padding-top: 54px;
}
.l-header.is-follow .l-header__inner {
  position: fixed;
  top: 0;
  transition: transform .2s ease 0s;
  transform: translateY(0);
}
.l-header__inner {
  position: absolute;
  top: 100%;
  right: 0;
  left: 0;
  z-index: 10;
  display: flex;
  margin: auto;
  padding: 6px 10px;
  max-width: calc(1440px + 32px);
  transform: translateY(-100%);
  align-items: center;
}
.l-header__inner::before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  margin-left: calc(-50vw + 50%);
  width: 100vw;
  border-bottom: solid 1px #be8149;
  background: #6e421c;
  box-shadow: rgba(0, 0, 0, .133) 0 1.6px 3.6px 0, rgba(0, 0, 0, .11) 0 .3px .9px 0;
  content: "";
}
@media screen and (min-width: 769px) {
  .l-header__inner {
    padding: 6px 16px;
  }
}

.l-header__logo {
  margin-right: auto;
  padding-right: 16px;
}

/* ----------------------------------------------------------------
    Navigation
----------------------------------------------------------------- */
/* .l-nav
----------------------------------------------- */
.l-nav {
  flex: 1 1 320px;
  overflow: hidden;
  min-width: 180px;
  background: #fff8ea;
  transition: height .2s ease 0s;
}
:root[data-script-enabled] .l-nav {
  position: fixed;
  top: 54px;
  right: 0;
  left: 0;
  z-index: 9;
}
@media screen and (max-width: 768px) {
  :root[data-script-enabled] .l-nav:not([id]) {
    display: none;
  }
}
:root:not([data-script-enabled]) .l-nav {
  margin: 0 -16px;
}
@media screen and (min-width: 769px) {
  .l-nav {
    margin-top: 30px;
    padding-top: 60px;
    background: transparent;
  }
  :root[data-script-enabled] .l-nav {
    position: relative;
    top: 0;
  }
}
.l-nav__inner {
  overflow: auto;
  padding: 20px 16px 0;
}
@media screen and (min-width: 769px) {
  .l-nav__inner {
    padding: 0;
  }
}
.l-nav__inner > :last-child {
  padding-bottom: 20px;
}
:root[data-script-enabled] .l-nav__inner {
  min-height: calc(100 * var(--vh) - 54px);
  max-height: calc(100 * var(--vh) - 54px);
}
@media screen and (min-width: 769px) {
  :root[data-script-enabled] .l-nav__inner {
    min-height: auto;
    max-height: none;
  }
}

/* ----------------------------------------------------------------
    Main
----------------------------------------------------------------- */
/* .l-main
----------------------------------------------- */
.l-main {
  position: relative;
}
.l-main__content {
  margin: auto;
  padding: 0 10px;
  max-width: calc(1440px + 32px);
}
@media screen and (min-width: 769px) {
  .l-main__content {
    display: flex;
    padding: 0 16px;
  }
}
.l-main__content:not(:first-child) {
  margin-top: 40px;
}
.l-main__content:first-child, .admin-toolbar + .l-main__content {
  margin-top: 0;
}
@media screen and (min-width: 769px) {
  .l-main__content:first-child, .admin-toolbar + .l-main__content {
    padding-top: 80px;
  }
  .l-main__content:first-child:not(:first-child), .admin-toolbar + .l-main__content:not(:first-child) {
    margin-top: auto;
  }
  .l-main__content:first-child .l-content__main, .admin-toolbar + .l-main__content .l-content__main {
    transform: translateY(-32px);
  }
  .l-main__content:first-child::before, .admin-toolbar + .l-main__content::before {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    height: 80px;
    background: #be8149;
    content: "";
  }
}

.l-main__main-visual {
  position: relative;
  display: flex;
  overflow: hidden;
  min-height: 200px;
  max-height: 360px;
  background: #be8149;
  justify-content: center;
  align-items: center;
}
.l-main__main-visual::after {
  display: block;
  padding-top: 20%;
  content: "";
}

@media screen and (min-width: 769px) {
  .l-main--404 .p-article {
    padding-bottom: 60px;
  }
}

.l-content {
  width: 100%;
}
@media screen and (min-width: 769px) {
  .l-content {
    padding-left: 24px;
    max-width: calc(100% - 180px);
  }
}
@media screen and (min-width: 1280px) {
  .l-content {
    display: flex;
    padding-left: 0;
  }
}
.l-content__main {
  position: relative;
  margin: 0 auto;
}
@media screen and (min-width: 1280px) {
  .l-content__main {
    padding: 0 24px;
    max-width: calc(100% - 240px);
    flex-basis: 100%;
  }
}

.l-content__inner {
  margin: auto;
  max-width: 800px;
}

.l-content__side {
  position: relative;
  margin-top: 30px;
  padding-top: 60px;
}
@media screen and (min-width: 1280px) {
  .l-content__side {
    min-width: 240px;
    max-width: 240px;
    flex-basis: 100%;
  }
}

.l-search {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: auto;
  padding: 0 10px;
  width: 100%;
}
.l-search:first-child, .admin-toolbar + .l-search {
  padding-top: 80px;
}
.l-search:first-child::before, .admin-toolbar + .l-search::before {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  height: 80px;
  background: #be8149;
  content: "";
}
.l-search__form {
  margin: auto;
  max-width: 630px;
  width: 100%;
  transform: translateY(-50%);
}
.l-search__form + .l-search__suggest {
  margin-top: -16px;
}

.l-search__details {
  display: flex;
  flex-direction: column;
  margin: auto;
  max-width: 630px;
  width: 100%;
  justify-content: center;
  align-items: center;
}
.l-search__details + .l-search__suggest {
  margin-top: 14px;
}

.l-search__suggest {
  max-width: 630px;
}

/* ----------------------------------------------------------------
    Aside
----------------------------------------------------------------- */
/* .l-side
----------------------------------------------- */
.l-side_inner {
  margin: auto;
  max-width: 800px;
}
.l-side_inner:not(:first-child) {
  margin-top: 30px;
}

/* ----------------------------------------------------------------
    Footer
----------------------------------------------------------------- */
/* .l-footer
----------------------------------------------- */
.l-footer {
  display: flex;
  flex-direction: column;
  background: #6e421c;
  align-items: center;
}
.l-footer:not(:first-child) {
  margin-top: 80px;
}

/* .l-footer-content
----------------------------------------------- */
.l-footer-content {
  padding: 30px 10px;
  max-width: calc(1440px + 32px);
  width: 100%;
  color: #fff8ea;
}
@media screen and (min-width: 769px) {
  .l-footer-content {
    display: flex;
    margin: -30px 0 0 -40px;
    padding: 50px 16px 30px;
    justify-content: space-around;
  }
}
.l-footer-content__column:not(:first-child) {
  margin-top: 30px;
}
@media screen and (min-width: 769px) {
  .l-footer-content__column {
    margin: 30px 0 0 40px;
    max-width: calc(50% - 40px);
  }
}
@media screen and (min-width: 769px) {
  .l-footer-content__column--mini {
    max-width: 480px;
  }
}

/* .l-footer-copy
----------------------------------------------- */
.l-footer-copy {
  padding: 6px 10px;
  width: 100%;
  background: #524438;
  color: #fff8ea;
  text-align: center;
  letter-spacing: .025em;
  font-size: 1.2rem;
}
@media screen and (min-width: 769px) {
  .l-footer-copy {
    padding: 6px 16px;
  }
}

/* .l-footer-pageTop
----------------------------------------------- */
.l-footer-pageTop {
  position: relative;
  z-index: 5;
  visibility: hidden;
  opacity: 0;
  transition: opacity .2s ease 0s, visibility .2s ease .2s;
  will-change: opacity;
}
.l-footer-pageTop.is-follow {
  visibility: visible;
  opacity: 1;
  transition: opacity .2s ease 0s;
}
.l-footer-pageTop__link {
  position: fixed;
  right: 10px;
  bottom: 10px;
  overflow: hidden;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: rgba(110, 66, 28, .6);
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%2012.81%2020%22%3E%3Cpath%20style%3D%22fill%3A%20%23fff%3B%22%20d%3D%22M12.52%209.67l-5.29-5.29a1%201%200%200%200-.77-.38%201%201%200%200%200-.76.35l-5.3%205.32a1%201%200%200%200%201.42%201.42l3.64-3.63v11.54a1%201%200%200%200%202%200v-11.54l3.63%203.63a1%201%200%200%200%201.42-1.42z%22%2F%3E%3Crect%20style%3D%22fill%3A%20%23fff%3B%22%20x%3D%225.4%22%20y%3D%22-5.4%22%20width%3D%222.01%22%20height%3D%2212.81%22%20rx%3D%221%22%20transform%3D%22translate(7.41%20-5.4)%20rotate(90)%22%2F%3E%3C%2Fsvg%3E");
