}
.l-footer-pageTop__link:hover, .l-footer-pageTop__link:active, .l-footer-pageTop__link:focus {
  background-color: #6e421c;
}
@media screen and (min-width: 769px) {
  .l-footer-pageTop__link {
    right: 16px;
  }
}

