/* .c-button-menu
----------------------------------------------- */
.c-button-menu {
  position: relative;
  overflow: hidden;
  width: 32px;
  height: 32px;
}
.c-button-menu::before, .c-button-menu::after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  margin: auto;
  content: "";
}
.c-button-menu::before {
  width: 20px;
  height: 14px;
